<script src="../../../config/index.js"></script>
<template>
  <div class="clearfix">
    <div class="operation">
      <div class="clearfix ">
        <el-form label-width="80px">
          <el-col :span="20">
            <el-form-item label="收款时间" >
              <el-date-picker
                type="date"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                placeholder="起始时间"
                v-model="tempData.startTime"
              ></el-date-picker>
              <span style="width:60px;">-</span>
              <el-date-picker
                type="date"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                placeholder="截止时间"
                v-model="tempData.endTime"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="资金类型">
              <el-select
                v-model="tempData.capitalType"
                @change="selectStatus"
                placeholder="请选择审核结果查询">
                <el-option label="全部" value="">全部</el-option>
                <el-option label="付款" value="0"></el-option>
                <el-option label="收款" value="1"></el-option>
                <el-option label="其他" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="支付类型">
              <el-select
                v-model="tempData.payType"
                @change="selectStatus"
                placeholder="请选择审核结果查询">
                <el-option label="全部" value="">全部</el-option>
                <el-option label="微信" value="0"></el-option>
                <el-option label="支付宝" value="1"></el-option>
                <el-option label="第三方支付" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="3" style="padding-left:40px">
            <el-button type="primary" @click="search" icon="el-icon-search">查询</el-button>
          </el-col>
          <el-col :span="1" style="padding-left:20px">
            <el-button type="primary" @click="editorDia('add')">新增</el-button>
          </el-col>
        </el-form>
      </div>
    </div>
    <div class="table">
      <el-table
        border
        :data="dataList"
        style="width: 100%;margin-top:20px;"
      >
        <el-table-column
          fixed
          label="序号"
          width="80"
          align="center"
          type="index" >
        </el-table-column>
        <el-table-column
          prop="capitalMoeny"
          label="资金金额"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="capitalType"
          label="资金类型"
          width="200"
          align="center" >
          <template slot-scope="scope">
            <span v-if="scope.row.capitalType == 0">付款</span>
            <span v-else-if="scope.row.capitalType == 1">收款</span>
            <span v-else-if="scope.row.capitalType == 2">其他</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="gatheringAccount"
          label="收款账户"
          width="250"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="gatheringName"
          label="收款名称"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="payAccount"
          label="支付账户"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="payName"
          label="支付名称"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="payType"
          label="支付类型"
          width="200"
          align="center" >
          <template slot-scope="scope">
            <span v-if="scope.row.payType == 0">微信</span>
            <span v-else-if="scope.row.payType == 1">支付宝</span>
            <span v-else-if="scope.row.payType == 2">第三方支付</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label="操作时间"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="editorDia('update', scope.row)">修改</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination
      :current-page="page.currentPage"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    >
    </pagination>

    <el-dialog
      title="编辑"
      :visible.sync="updateStatus"
      width="550px"
      center
      :modal-append-to-body="false"
    >
      <el-form :model="operTreeData" ref="operTreeData">
        <el-form-item label="资金金额">
          <el-input v-model="updateData.capitalMoeny">
          </el-input>
        </el-form-item>
        <el-form-item label="资金类型">
          <el-select v-model="updateData.capitalType" placeholder="请选择资金类型">
            <el-option label="默认" value=""></el-option>
            <el-option label="付款" value="0"></el-option>
            <el-option label="收款" value="1"></el-option>
            <el-option label="其他" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收款账户">
          <el-input v-model="updateData.gatheringAccount"></el-input>
        </el-form-item>
        <el-form-item label="收款名称">
          <el-input v-model="updateData.gatheringName"></el-input>
        </el-form-item>
        <el-form-item label="付款账户">
          <el-input v-model="updateData.payAccount"></el-input>
        </el-form-item>
        <el-form-item label="付款名称">
          <el-input v-model="updateData.payName"></el-input>
        </el-form-item>
        <el-form-item label="请选择支付类型" class="m-select">
          <el-select v-model="updateData.payType" placeholder="请选择支付类型">
            <el-option label="默认" value=""></el-option>
            <el-option label="微信" value="0"></el-option>
            <el-option label="支付宝" value="1"></el-option>
            <el-option label="第三方支付" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="editor" type="success">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import { sysCapitalQueryPage, sysCapitalUpdate, sysCapitalAdd, managerUpdateIsAudit } from "@/api/index.js";
  import pagination from "@/components/pagination";
  export default {
    components: {
      pagination
    },
    data() {
      return {
        dataList: [],
        //搜索时的临时数据
        tempData: {
          capitalType: '',
          payType: '',
          startTime: '',
          endTime: ''
        },
        updateData: {
          capitalType: '0',
          payType: '0'
        },
        updateStatus: false,
        //搜索时的数据
        serData: {},
        //分页信息
        page: {
          pageNum: 10,
          currentPage: 1,
          pageSize: 5
        }
      };
    },
    methods: {
      //加载数据
      initData(page, size) {
        sysCapitalQueryPage({
          page: page || this.page.currentPage,
          size: size || this.page.pageSize,
          capitalType: this.tempData.capitalType,
          payType: this.tempData.payType,
          startTime: this.tempData.startTime,
          endTime: this.tempData.endTime
        }).then(res => {
          this.dataList = res.rows;
          this.page.pageNum = res.count;
        });
      },
      editorDia(type, row){
        this.updateStatus = true;
        if (type == 'update') {
          this.updateData = row;
          this.updateData['capitalType'] = String(row['capitalType'])
          this.updateData['payType'] = String(row['payType'])
        }
      },
      add() {
        sysCapitalAdd(this.updateData).then( res => {
          this.initData(1);
        } )
      },
      editor() {
        sysCapitalUpdate(this.updateData).then( res => {
          this.updateStatus=false;
          this.initData(1);
        })
      },
      //页数改变时的回调
      currentChange(val) {
        this.page.currentPage = val;
        this.initData();
      },
      //每页总数改变时的回调
      sizeChange(val) {
        this.page.pageSize = val;
        this.initData(1);
      },
      selectStatus() {
        this.initData(1);
      },
      //条件筛选店铺
      search() {
        this.initData(1);
      }
    },
    beforeMount() {
      this.initData();
    }
  };
</script>

<style>
  .addBatchBtn a {
    color: #ffffff;
  }
  .m-select {

  }
  .content {
    /*min-width: 1200px;*/
    background: rgb(255, 255, 255);
    padding: 26px 30px;
    position: relative;
    /*min-height: 800px;*/
  }
  h4 {
    text-align: left;
    color: #333333;
    font-size: 18px;
    font-family: PingFang-SC-Bold;
    padding-bottom: 25px;
    border-bottom: 1px solid #e1e1e1;
  }
</style>



